:root {
  --primary: #d81e5b;
  --secondary: #bda0bc;
  --dark: #470b7a;
  --light: #eee;
  --green: #470b7a;
  --red: red;
}

@font-face {
  font-family: "Digit";
  src: url("font/Matricha.ttf");  
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Fira Sans', sans-serif;
}

body {
  background-color: var(--light);
}

.App {
  display: flex;
  min-height: 100vh;
  align-items: center;
  justify-content: center;
  padding: 16px;  
}

.calculator {
  width: 100%;
  max-width: 400px;
  background-color: var(--secondary);
  border-radius: 16px;
  border: 2px solid;
  overflow: hidden;
  box-shadow: 50px 50px 64px rgba(0, 0, 0, 0.2);
}

.display {
  padding: 14px;
  text-align: right;
  background: rgb(183,222,237); /* Old browsers */
  background: -moz-linear-gradient(top,  rgba(183,222,237,1) 0%, rgba(113,206,239,1) 39%, rgb(112, 197, 223) 62%, rgba(183,222,237,1) 100%); /* FF3.6-15 */
  background: -webkit-linear-gradient(top,  rgba(183,222,237,1) 0%,rgba(113,206,239,1) 39%,rgba(122,197,223,1) 62%,rgba(183,222,237,1) 100%); /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(to bottom,  rgba(183,222,237,1) 0%,rgba(113,206,239,1) 39%,rgba(122,197,223,1) 62%,rgba(183,222,237,1) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#b7deed', endColorstr='#b7deed',GradientType=0 ); /* IE6-9 */
  border: 2px solid;
  color: var(--green);
  font-family: Digit;
  font-size: 35px;
  font-weight: 500;
  text-overflow: ellipsis;
  border-radius: 16px;
  margin: 8px;  
}

.display span {
  font-family: Digit;
  font-size: 35px;
  font-weight: 500;
  color: var(--red);
}

button {
  appearance: none;
  border: none;
  outline: none;
  color: #FFF;
  font-size: 20px;
  padding: 16px;
  cursor: pointer;
  transition: 0.4s;
  border: 1px solid;
}

button:hover {
  background-image: linear-gradient(to right top, #bda0ba, #ab83ac, #98689f, #834d93, #6b3288, #6b3288, #6b3288, #6b3288, #834d93, #98689f, #ab83ac, #bda0ba);
}

.operators {
  display: flex;
}

#operators {
  border-radius: 16px;
  margin: 8px;
  background-color: var(--primary);
  font-weight: 700;
  letter-spacing: 0.1em;
}

#operators:hover {
  background-image: linear-gradient(to right top, #bda0ba, #c28eb2, #c97aa6, #d06594, #d54c7e, #d54c7e, #d54c7e, #d54c7e, #d06594, #c97aa6, #c28eb2, #bda0ba);
}

.digits {
  display: flex;
  flex-wrap: wrap;
}

.digits button {
  border-radius: 16px;
  margin: 8px;
  flex: 1 1 28%;
  
  background-color: var(--dark);
}

.head {
  display: flex;
  flex-wrap: wrap;
}

.head button {  
  border-radius: 16px;
  margin: 8px;
  margin-bottom: 0px;
  cursor: none; 
  background-color: var(--dark);
}

.head button {    
  flex: 1 1 28%;  
}

.head-text  {  
  display: flex;
  flex-wrap: wrap;  
  color: var(--dark);
}

.head-text div {    
  text-align: center;
  border-radius: 16px;
  margin: 8px;
  margin-top: 0px;
  cursor: none; 
  color: var(--white);
  margin-bottom: 10px;
  flex: 1 1 28%;  
}